import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import styled from "styled-components";
import TextField from "material-ui/TextField";
import deleteBtn from "material-ui/svg-icons/action/delete";
import Download from "material-ui/svg-icons/action/get-app";

const LI = styled.li`
  list-style: none;
  padding: 1rem;
  font-size: 1.6rem;
  font-family: Roboto, sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &:hover {
    background: #f5f5f5;
  }
  & span {
    flex-grow: 1;
  }
  & svg {
    cursor: pointer;
    fill: #bdbdbd !important;
  }
  & svg:hover {
    fill: #607d8b !important;
  }
  & input {
    border: none!important;
  }
`;

const Delete = styled(deleteBtn)`
  align-items: flex-end;
`;

const FileItem = ({ fileName }) => {
  const destroyClickedElement = event => {
    document.body.removeChild(event.target);
  };

  const saveTextAsFile = (text, filename) => {
    var textFileAsBlob = new Blob([text], { type: "text/plain" });
    var downloadLink = document.createElement("a");
    downloadLink.download = filename;
    downloadLink.innerHTML = "Download File";
    if (window.webkitURL != null) {
      // Chrome allows the link to be clicked
      // without actually adding it to the DOM.
      downloadLink.href = window.webkitURL.createObjectURL(textFileAsBlob);
    } else {
      // Firefox requires the link to be added to the DOM
      // before it can be clicked.
      downloadLink.href = window.URL.createObjectURL(textFileAsBlob);
      downloadLink.onclick = destroyClickedElement;
      downloadLink.style.display = "none";
      document.body.appendChild(downloadLink);
    }
    downloadLink.click();
  };

  return (
    <LI>
      <span>{fileName}</span>
      <Delete />
      <Download onClick={
        saveTextAsFile()
      }/>
    </LI>
  );
};

FileItem.propTypes = {
  fileName: PropTypes.string
};

export default FileItem;
